<template>
    <div>
        <div class="list list-up field-part">
            <cell class="item" v-for="item in messageList" 
                :title=" item.title " :label=" item.dateTime " :key=" item.id " to="detail" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/home/tip-ico-act@2x.png" >
            </cell>
        </div>
        <div class="list field-part">
            <cell class="item" v-for="item in messageList" 
                :title=" item.title " :label=" item.dateTime " :key=" item.id " to="detail" is-link>
                <img class="cell-icon" slot="icon" src="../../../assets/img/home/tip-ico-ver@2x.png" >
            </cell>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/home/message-center';

export default {
    data() {
        return {
            messageList: [],
            list: [],
            loading: false
        };
    },
    created() {
      this.list = Object.assign({}, this.$store.state.messageCenter.search).data;
      if (this.list.length == 0) {
        this.list = [];
        this.getMessageList();
      }
    },
    computed: {
    },
    components: {
    },
    methods: {
    	getMessageList() {
    		service.getMessagetIndex().then((res) => {
	            this.messageList = res.data.data;
	        }, (err) => {
	            console.log( err );
	        });
    	},
    	loadMore() {
            console.log("loadMore");
            this.loading = true;
    		  	this.getMessageList();
            setTimeout(() => {
              for (var i = 0; i < this.messageList.length; i++) {
                this.list.push(this.messageList[i]);
              }
              this.loading = false;
            }, 500);
          }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateMessageCenterSearch', this.list );
        }else{
            this.$store.dispatch('clearMessageCenterSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">

    .list
    {
        .item
        {
            img
            {
                width: 60px;
                height: 60px;
            }
            .cell-title
            {
                margin-left: 30px!important;
            }
        }
    }
    .list-up {
        .cell{
            position: relative;
            background-color: #F0F0F0 !important;
            // box-shadow: inset 0 0 0 0 #E6E5EA;
        }
        .cell:last-child
        {
            @include mini-border(bottom, #E6E5EA, after);
            &:after
            {
                left: 50px;
            }
        }
    }
    .cell-icon:after {
        content: '';
        position: absolute;
    }


</style>



