import axios from 'axios';
import api from './api';

const defaultLoading = '...';

axios.defaults.baseURL = '/mock';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';

axios.defaults.transformRequest = function (data, headers) {
    /*  loading */
    let loadingText = '';
    if (data && data.showLoading === true) {
        loadingText = defaultLoading;
    } else if (data && typeof data.showLoading === 'string') {
        loadingText = data.showLoading;
    }
    if (data && data.showLoading) {
        // Indicator.open({
        //     text: loadingText,
        //     spinnerType: 'fading-circle'
        // });
    }
    /*   */
    /*  Login_name 340101199105050073  340101199001013999 34122719720304441X */
    if (data && data.method) {
        return `method=${data.method}&Login_name=34122719720304441X&content=${data.content ? encodeURI(JSON.stringify(data.content)) : '{}'}`;
    }
    return data;
};

// Add a request interceptor
axios.interceptors.request.use(function (config) {
    /*  */
    if (config.baseURL === '/mock' && config.data && config.data.url) {
        config.url = config.baseURL + config.data.url;
    }
    // Do something before request is sent
    return config;
}, function (error) {
    // Do something with request error
    return Promise.reject(error);
});

// Add a response interceptor
axios.interceptors.response.use(function (response) {
    /*  */
    if (response.data && 'RESULTCODE' in response.data) {
        if (response.data.RESULTCODE !== '0') {
            return Promise.reject({ RESULTCODE: response.data.RESULTCODE, MESSAGE: response.data.MESSAGE });
        } else {
            console.log(response.data.data);
            return JSON.parse( response.data.data );
        }
    }
    // Do something with response data
    return response;
}, function (error) {
    // Do something with response error
    return Promise.reject(error);
});


const ajax = {
    post(url, data) {
        return new Promise(function (resolve, reject) {
            axios.post(url, data).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    },
    /*    */
    postBeforeLoginQuery(method, content, url) {
        return new Promise(function (resolve, reject) {
            axios.post(api.beforeLoginQuery, { method, content, url }).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    },
    /*  []  */
    postBeforeLoginUpdate(method, content, url) {
        return new Promise(function (resolve, reject) {
            axios.post(api.beforeLoginUpdate, { method, content, url }).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    },
    /*    */
    postBeforeLoginQueryList(method, content, url) {
        return new Promise(function (resolve, reject) {
            axios.post(api.beforeLoginQueryList, { method, content, url }).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    },
    /*    */
    postAfterLoginQuery(method, content, url) {
        return new Promise(function (resolve, reject) {
            axios.post(api.afterLoginQuery, { method, content, url }).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    },
    /*  []  */
    postAfterLoginUpdate(method, content, url) {
        return new Promise(function (resolve, reject) {
            axios.post(api.afterLoginUpdate, { method, content, url }).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    },
    /*    */
    postAfterLoginQueryList(method, content, url) {
        return new Promise(function (resolve, reject) {
            axios.post(api.afterLoginQueryList, { method, content, url }).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    }
};
export default ajax;


