<template>
    <div class="detail">
        <h2>{{title}}</h2>
        <h6>{{dateTime}}</h6>
        <div class="content" @click="showShare()">
            <div class="tip-detail-img" v-if="isImg=='show'"></div>
            <p class="tip-detail-content">{{content}}</p>
            <div class="tip-detail-img" v-if="isImg=='show'"></div>
            <p class="tip-detail-content">{{content}}</p>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/home/message-center';

export default {
    data() {
        return {
            title: '',
            dateTime: '',
            content: '',
            isImg: ''
        };
    },
    created() {
      service.getMessagetDetail().then((res) => {
          this.title = res.data.title;
          this.dateTime = res.data.dateTime;
          this.content = res.data.content;
          this.isImg = res.data.isImg;
      }, (err) => {
          console.log( err );
      });
    },
    computed: {
    },
    components: {
    },
    methods: {
        showShare (){
            alert('xx');
        }
    }
};
</script>


<style lang="scss">
.detail {
    position: relative;
    padding-top: 30px;
    height: 100%;
    background-color: #fff;
    .content {
        background-color: #fff;
        padding-bottom: 50px;
    }
    h2,h6 {
        text-align: center;
        font-family: PingFangSC-Medium;
        font-size: 20px;
        color: #272727;
        line-height: 56px;
    }
    h6 {
        font-family: SFUIText-Regular;
        font-size: 15px;
        color: #666666;
        line-height: 36px;
    }
    .tip-detail-img {
        background: #f3f8f9;
        width: 650px;
        height: 230px;
        margin: 50px auto 0px auto;
    }
    .tip-detail-content {
        width: 650px;
        margin: 34px auto 0px auto;
        font-family: PingFangSC-Regular;
        font-size: 15px;
        color: #272727;
        line-height: 60px;
    }
}
</style>



