const Touch = {
    start: {
        x: 0,
        y: 0
    },
    changeDis: 30,
    dsitance: 0,
    touchstart(e, callBack) {
        let touch = e.targetTouches[0];
        this.start.x = touch.pageX;
        this.start.y = touch.pageY;
        // console.log(this.start);
        callBack();
    },
    touchmove(e, callBack) {
        let touch = e.targetTouches[0];
        let moveX = touch.pageX;
        // 
        this.distance = moveX - this.start.x;
        // console.log(this.distance);
        callBack(this.distance);
    },
    touchend(e, callBack) {
        let dis = Math.abs(this.distance);
        if (dis < this.changeDis) {
            callBack(false, this.distance);
        } else {
            callBack(true, this.distance);
        }
        this.start = {
            x: 0,
            y: 0
        };
        this.distance = 0;
    }
};

export default Touch;


