<template>
    <div class="type" :class="{ show : showPanel }">
        <div class="cover" @click="close"></div>
        <div class="main">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click="close()"></a>
                <h2></h2>
            </div>
            <div class="content">
                <div class="panel-group">
                    <div v-for="item in options" class="panel" @click="link(item)">
                        <div :class=" 'icon icon-menu-' + item.MENU_NO "></div>
                        <div class="title" v-text="item.title"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'type',
        props: {
            options: { type: Array, required: true }
        },
        data() {
            return {
                showPanel: false
            };
        },
        computed: {
            modules() {
                return this.options;
            }
        },
        methods: {
            show() {
                this.showPanel = true;
            },
            close() {
                this.showPanel = false;
            },
            link(item) {
                this.$router.push({ name: item.link });
            }
        }
    };

</script>

<style lang="scss" scoped>
    $item-height: 120px;
    .type {
        position: fixed;
        top: 100%;
        left: 0px;
        height: 100%;
        width: 100%;
        z-index: 999;
        opacity: 0;


        @include transition(opacity 0.3s);

        &.show {
            top: 0px;
            opacity: 1;
        }

        .cover {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .main {
        position: absolute;
        left: 0px;
        right: 0px;
        bottom: 0px;
        z-index: 9;
        background-color: #fff;

        .hd {
            position: relative;
            height: $item-height;
            line-height: $item-height;

            @include mini-border(bottom);

            h2 {
                margin: 0px;
                font-size: 16px;
                color: #2A3342;
                text-align: center;
            }

            .btn-cancel {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 50px;
                font-size: 13px;
            }
        }

        .content {
            position: relative;
            width: 100%;

            .panel-group {
                box-sizing: border-box;

                .panel {
                    float: left;
                    padding: 40px 0px 30px;
                    width: 33.333%;
                    text-align: center;

                    .icon {
                        position: initial;
                        top: initial;
                        left: initial;
                        transform: initial;
                        width: 64px;
                        height: 64px;
                        margin: 0 auto;
                        background-repeat: no-repeat;
                        background-position: center;
                        background-size: 100% 100%;
                    }

                    .title {
                        margin-top: 10px;
                        font-size: 13px;
                        line-height: 36px;
                        color: #272727;
                        letter-spacing: 0;
                    }
                }
            }
        }
    }
</style>


