<template>
    <div class="shortcut-setting" :class="{ 'show-setting' : dataForm.setting }">
        <div class="cell-action inline">
            <mt-button  size="large" @click="cancelSetting()">{{ dataForm.setting ? '' : '' }}</mt-button>
            <mt-button type="primary" size="large" @click.stop.prevent="confirmSetting">{{ dataForm.setting ? '' : '' }}</mt-button>
        </div>
        <div class="menu-group">
            <h2 class="hd"></h2>
            <h4 class="wran" v-if="this.dataForm.setting">(7)</h4>
            <draggable element="ul" class="list" :class="[ 'row-' + Math.ceil(menuDefaultList.length  / 4), { dragging: dataForm.draggableStart }]" 
                :options="draggable"
                v-model="menuDefaultList"  >
                <transition-group type="transition" :name="'draggable'">
                    <li class="item" v-for="(item, index) in menuDefaultList" :key=" item.MENU_NO ">
                        <i :class=" 'icon icon-menu-' + item.MENU_NO "></i>
                        <cite>{{ item.MENU_NAME }}</cite>
                        <a href="javascript:;" class="btn-remove" @click="removeMenu(item, index )" ></a>
                    </li>
                </transition-group>
            </draggable>

            <template v-for=" group in menuGroupList ">
                <h2 class="hd">{{ group.MENU_NAME }}</h2>
                <ul class="list" :class=" 'row-' + Math.ceil(menuGroupMap[ group.MENU_NO ].length  / 4) ">
                    <li class="item" v-for="(item, index) in menuGroupMap[ group.MENU_NO ] " :key=" item.MENU_NO ">
                        <i :class=" 'icon icon-menu-' + item.MENU_NO "></i>
                        <cite>{{ item.MENU_NAME }}</cite>
                        <a href="javascript:;" v-if=" menuNoDefaultList.indexOf( item.MENU_NO ) > -1 " class="btn-checked" ></a>
                        <a href="javascript:;" v-else class="btn-add" @click="addMenu(item, index )" ></a>
                    </li>
                </ul>
            </template>
        </div>
    </div>
</template>

<script>

import CONST from '@/assets/js/const';

import draggable from 'vuedraggable';

export default {
    data () {
        return {
            dataForm: {
                setting: false
            },
            menuDefaultList: [],
            menuDefaultCloneList: [],
            menuGroupList: [],
            menuGroupMap: {}
        };
    },
    created (){
        console.log( this.$route.meta.menuAuth );
        let _userMenuSetting = this.$ls.getItem( CONST.LS_USER_MENU_KEY );
        this.menuDefaultList = Object.assign( [], _userMenuSetting || this.$store.state.menuAuth.menuHomeDefault );
        let _menuList = this.$store.state.menuAuth.menuList;
        let _menuGroupList = _menuList.filter((item)=>{ return item.MENU_TYPE.split('').indexOf('3') > -1 });
        let _menuGroupMap = {};
        _menuGroupList.forEach((item)=>{
            _menuGroupMap[ item.MENU_NO ] = [];
        });
        _menuList.forEach((item)=>{
            if( item.GROUP_MENU in _menuGroupMap ){
                _menuGroupMap[ item.GROUP_MENU ].push( item );
            }
        });
        this.menuGroupList = _menuGroupList;
        this.menuGroupMap = _menuGroupMap;
    },
    computed: {
        draggable () {
            return {
                disabled: !this.dataForm.setting
            };
        },
        menuNoDefaultList () {
            let _temp = [];
            this.menuDefaultList.forEach((item)=>{
                _temp.push( item.MENU_NO );
            });
            return _temp;
        }
    },
    methods: {
        confirmSetting () {
            this.dataForm.setting = !this.dataForm.setting;
            if(this.dataForm.setting) {
                this.menuDefaultCloneList = Object.assign([], this.menuDefaultList);
                return;
            }
            this.$ls.setItem(CONST.LS_USER_MENU_KEY, this.menuDefaultList );
        },
        cancelSetting() {
            if( this.dataForm.setting ) {
                this.dataForm.setting = false;
                this.menuDefaultList = Object.assign([], this.menuDefaultCloneList);
            } else {
                this.$router.replace({ path: '/home' });
            }
        },
        addMenu ( menu ) {
            if ( this.menuDefaultList.length >= 7 ) {
                this.$hybrid.call({
                    method: 'toast',
                    params: {
                        message: '7',
                        duration: 1000
                    }
                });
                return;
            }
            let _index = this.menuDefaultList.findIndex((item) => {
                return item.MENU_NO == menu.MENU_NO;
            });
            if ( _index === -1) {
                this.menuDefaultList.push( Object.assign( {}, menu ) );
            }
        },
        removeMenu ( menu, index ) {
            if ( this.menuDefaultList.length <= 3 ) {
                this.$hybrid.call({
                    method: 'toast',
                    params: {
                        message: '3',
                        duration: 1000
                    }
                });
                return;
            }
            this.menuDefaultList.splice(index, 1);
        }
    },
    components: {
        draggable
    }
};
</script>

<style lang="scss" scoped>
    
    .menu-group
    {
        .hd
        {
            padding-left: 50px;
            height: 80px;
            line-height: 80px;
            font-size: 15px;
            color: #666666;
        }
        .wran
        {
            margin-top: -15px;
            margin-bottom: 20px;
            padding-left: 50px;
            font-size: 13px;
            color: #B5B5B5;
        }
        .list
        {
            background-color: #fff;
            @include clearfix();
            .item
            {
                float: left;
                width: 25%;
                position: relative;
                padding-top: 122px;
                // padding-bottom: 14px;
                height: 188px;
                text-align: center;
                .icon
                {
                    top: 40px;
                    left: 50%;
                    @include transform(translateX(-50%));
                }
                cite
                {
                    display: block;
                    padding: 0px 4px;
                    font-size: 13px;
                    color: #272727;

                    @include ellipsis();
                }


                a
                {
                    display: none;
                    position: absolute;
                    top: 28px;
                    right: 38px;
                    width: 50px;
                    height: 50px;

                    &:before
                    {
                        content: '';
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        width: 30px;
                        height: 30px;
                        
                        @include image-set('../../assets/img/home/icon-menu-add');

                        @include transform(translate(-50%, -50%));

                        @include bg-offset-center();
                    }
                }
                .btn-remove
                {
                    &:before
                    {
                        @include image-set('../../assets/img/home/icon-menu-remove');
                    }
                }
                .btn-checked
                {
                    &:before
                    {
                        @include image-set('../../assets/img/home/icon-menu-checked');
                    }
                }
            }
            .item
            {
                position: relative;

                @include mini-border(right, #F6F6F6, before);
                @include mini-border(bottom, #F6F6F6, after);
            }
            .item:nth-of-type(4n+4)
            {
                &:before
                {
                    content: none;
                }
            }

            @for $i from 1 through 10 
            {
                &.row-#{$i}
                {
                    .item:nth-of-type(n+#{$i*4-3})
                    {
                        &:after
                        {
                            content: none;
                        }
                    }
                }
            }
            .item.sortable-ghost
            {
                background-color: #F7F7F7;
            }
            .item.sortable-drag.sortable-chosen
            {
                background-color: #FFFFFF;
                box-shadow: 0 4px 10px 0 rgba(0,0,0,0.10);
            }
        }
    }

    .shortcut-setting.show-setting
    {
        .menu-group
        {
            .list
            {
                .item
                {
                    a
                    {
                        display: block;
                    }
                }
            }
        }
    }

    .draggable-move 
    {
        transition: transform 0.3s;
    }


</style>





