<template>
    <div class="life">
        <!-- e -->
        <div class="page-entry"></div>

        <div class="top">
            <p class="title"></p>
            <div class="manager">
                <router-link to="history" tag="p" append>
                    
                </router-link>
                <p @click="managerOffen">{{ manager === true ? '' : '' }}</p>
            </div>
        </div>

        <div class="module-offen">
            <template v-for="item in offtenList">
                <div class="offen" :class="{'move': move === item.id}" data-anch="">
                    <div class="cell-content" @touchstart="touchstart($event,item)" @touchmove.stop.prevent="touchmove($event,item)" @touchend="touchend($event,item)">
                        <cell :title="setTitle(item)" @click.native="doOffen(item)" :label="setLabel(item)" isLink>
                            <i slot="icon" class="offen-icon" :class=" 'icon icon-menu-' + map[item.PAYMENT_TYPE].no "></i>
                        </cell>
                    </div>
                    <div :ref="item.id" class="offen-manager">
                        <div class="manager-group">
                            <span v-for="items in managerList" @click="items.handler(item)">
                                {{items.content}}
                            </span>
                        </div>
                    </div>
                </div>
            </template>
        </div>

        <div class="module-wrap">
            <template v-for="item in moduleList">
                <div class="module" @click="doLife(item)">
                    <div :class=" 'icon icon-menu-' + item.MENU_NO "></div>
                    <div class="wrap">
                        <p class="title">{{item.title}}</p>
                        <p class="add" v-if="item.add"></p>
                    </div>

                </div>
            </template>
        </div>

        <!--  -->
        <div class="field-action">
            <mt-button type="default" size="large" @click="getMore"></mt-button>
        </div>

        <life-more ref="more" :options="moduleList"></life-more>
    </div>
</template>

<script>
    import * as services from '@/services/life';
    import LifeMore from './life-more';
    import Touch from './touch';
    export default {
        name: 'life',
        components: {
            LifeMore
        },
        data() {
            return {
                moduleList: [],
                offtenList: [],
                manager: false,
                map: {
                    D: { title: '', no: '306', module: 'power', link: 'LifePower', links: 'LifePowerInfo' },
                    S: { title: '', no: '305', module: 'water', link: 'LifeWater', links: 'LifeWaterInfo' },
                    L: { title: '', no: '311', module: 'heat', link: 'LifeHeat', links: 'LifeHeatInfo' },
                    R: { title: '', no: '307', module: 'gas', link: 'LifeGas', links: 'LifeGasInfo' },
                    Y: { title: '', no: '308', module: 'television', link: 'LifeTelevision', links: 'LifeTelevisionInfo' }
                },
                managerList: [
                    {
                        content: '',
                        handler: (item) => this.editInfo(item)
                    }, {
                        content: '',
                        handler: (item) => this.delInfo(item)
                    }
                ],
                move: null
            };
        },
        created() {
            /**  **/
            services.getLifeCell({ showLoading: '...' })
                .then(response => {
                    this.moduleList = response.data;
                });
            /**  **/
            services.getLifeOften({ showLoading: '...' })
                .then(response => {
                    this.offtenList = response.data;
                });
        },
        methods: {
            setTitle(item) {
                let mark = item.REASON || "";
                return mark === "" ? this.map[item.PAYMENT_TYPE].title : mark;
            },
            setLabel(item) {
                let name = '*' + item.USER_NE.substring(1);
                return item.USER_NO + "  |  " + name;
            },
            translate3d(X) {
                return `translate3d(${X}px, -50%, 0)`;
            },
            // 
            touchstart(e, item) {
                let self = this;
                Touch.touchstart(e, function () {
                    self.move = item.id;
                });
            },
            touchmove(e, item) {
                let self = this;
                if (this.manager) {
                    return false;
                }
                let width = this.$refs[item.id][0].clientWidth;
                let target = document.querySelector('.move').getAttribute('data-anch');
                Touch.touchmove(e, function (dis) {
                    // 
                    if (target === "") {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(dis);
                    } else if (target === item.id) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(-width + dis);
                    }
                    // 
                    if (target === "" && dis > 0) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(0);
                    } else if (target === "" && dis < -width) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(-width);
                    } else if (target === item.id && dis < 0) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(-width);
                    } else if (target === item.id && dis > width) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(0);
                    }
                });
            },
            touchend(e, item) {
                let self = this;
                if (self.manager) {
                    return false;
                }
                let width = self.$refs[item.id][0].clientWidth;
                let target = document.querySelector('.move').getAttribute('data-anch');
                Touch.touchend(e, function (flag, dis) {
                    if (target === "" && flag && dis < 0) {
                        let anch = document.querySelectorAll('.offen');
                        // cell
                        let el = Array.prototype.slice.call(anch).filter(item => {
                            return item.getAttribute('data-anch') !== '';
                        });
                        if (el.length > 0) {
                            for (let val of el) {
                                val.setAttribute('data-anch', '');
                                val.firstElementChild.style.webkitTransform = self.translate3d(0);
                            }
                        }
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(-width);
                        document.querySelector('.move').setAttribute('data-anch', item.id);
                    } else if (target === "" && !flag) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(0);
                    } else if (target === item.id && flag && dis > 0) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(0);
                        document.querySelector('.move').setAttribute('data-anch', '');
                    } else if (target === item.id && !flag) {
                        document.querySelector('.move .cell-content').style.webkitTransform = self.translate3d(-width);
                    }
                });
            },
            managerOffen() {
                this.manager = !this.manager;
                // 
                let anch = document.querySelectorAll('.offen');
                let list = Array.prototype.slice.call(anch).filter(item => {
                    return item.getAttribute('data-anch') !== '';
                });
                if (list.length > 0) {
                    for (let val of list) {
                        val.setAttribute('data-anch', '');
                        val.firstElementChild.style.webkitTransform = this.translate3d(0);
                    }
                }
                let el = document.querySelectorAll('.offen .offen-manager');
                if (this.manager) {
                    for (let val of el) {
                        val.style.zIndex = '10';
                        val.style.backgroundColor = '#fff';
                    }
                } else {
                    for (let val of el) {
                        val.style.zIndex = '0';
                        val.style.backgroundColor = 'inherit';
                    }
                }
            },
            // 
            getMore() {
                this.$refs.more.show();
            },
            // 
            doLife(item) {
                this.$router.push({ name: item.link });
            },
            // 
            doOffen(item) {
                let ls = this.map[item.PAYMENT_TYPE].module;
                let { COMPANY_WHOLECODE, COMPANY_CODE, AREA_CODE, USER_NO, COMPANY_NAME } = item;
                let params = {
                    BUSINO: 'COMPANY_CODE',
                    GRIDID: 'COMPANY_WHOLECODE',
                    USERNO: 'USER_NO',
                    CITYCODE: 'AREA_CODE'
                };
                let setLs = {
                    companyName: COMPANY_NAME,
                    no: USER_NO
                };
                /**  **/
                services.getPowerinfo(params)
                    .then(res => {
                        let _data = res.data;
                        let obj = Object.assign({}, _data, setLs);
                        this.$ls.setItem(ls, obj);
                        this.$router.push({ name: this.map[item.PAYMENT_TYPE].links, query: { history: -3 } });
                    }, error => {
                        console.log(error);
                    });
            },
            editInfo(item) {
                this.$ls.setItem('life-offen-list', item);
                this.$router.push({ name: 'LifeManager' });
            },
            delInfo(item) {
                this.$hybrid.call({
                    method: 'confirm',
                    params: {
                        title: '?',
                        message: ''
                    },
                    callback: (params) => { }
                });
            }
        }
        // ,
        // beforeRouteLeave(to, from, next) {
        //     if (this.swiper.length !== 0 && to.name !== 'LifeManager') {
        //         next(false);
        //     } else {
        //         next(true);
        //     }
        // }
    };

</script>

<style lang="scss" scoped>
    .page-entry {
        height: 240px;
        @include image-set('../../assets/img/life/banner');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100% 100%;
    }

    .top {
        padding: 32px 30px 24px;
        height: 96px;

        .title {
            float: left;
            font-size: 14px;
            line-height: 40px;
            color: #272727;
        }

        .manager {
            float: right;
            padding: 0 16px;

            p {
                display: inline-block;
                margin: 0 20px;
                font-size: 13px;
                line-height: 40px;
                color: #308CF8;
            }
        }
    }

    .module-offen {
        margin: 0 30px 10px;

        .offen+.offen {
            margin-top: 20px;
        }

        .offen {
            position: relative;
            height: 140px;
            border-radius: 5px;


            .offen-manager {
                position: absolute;
                top: 0;
                right: 0;
                padding-left: 30px;
                padding-right: 30px;
                height: 140px;
                color: #308CF8;

                .manager-group {
                    position: relative;
                    top: 50%;
                    transform: translateY(-50%);

                    span+span {
                        margin-left: 40px;
                    }
                }
            }

            .cell-content {
                position: absolute;
                top: 50%;
                left: 0;
                right: 0;
                transform: translateY(-50%);
                background-color: #fff;
                height: 140px;
                border-radius: 5px;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.03);
                z-index: 3;
                /* -webkit-transition: transform .2s;
                -moz-transition: transform .2s;
                transition: transform .2s;  */
                .cell {
                    position: relative;
                    top: 50%;
                    transform: translateY(-50%);
                    background-color: initial;
                }
            }

            .offen-icon {
                position: initial;
                top: initial;
                left: initial;
                transform: initial;
                height: 64px;
                width: 64px;
                margin-left: -28px;
                margin-right: 34px;
                line-height: 140px;
            }
        }
    }

    .module-wrap {
        margin: 0 30px;
        box-sizing: border-box;
        @include clearfix();

        .module {
            float: left;
            margin: 10px 0;
            padding: 24px 20px;
            height: 140px;
            width: 48%;
            background: #FFFFFF;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.03);
            border-radius: 5px;

            .icon {
                position: initial;
                top: initial;
                left: initial;
                transform: initial;
                display: inline-block;
                width: 44px;
                height: 44px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: 100% 100%;
                vertical-align: top;
            }

            .wrap {
                margin-left: 16px;
                display: inline-block;

                .title {
                    font-size: 16px;
                    line-height: 44px;
                    color: #272727;
                }

                .add {
                    margin-top: 8px;
                    font-size: 13px;
                    line-height: 36px;
                    color: #999999;
                }
            }
        }

        .module:nth-child(2n+2) {
            margin-left: 4%;
        }
    }
</style>


